@import url(./__image/profile__image.css);
@import url(./__info/profile__info.css);
@import url(./__title-container/profile__title-container.css);
@import url(./__title/profile__title.css);
@import url(./__edit/profile__edit.css);
@import url(./__edit-icon/profile__edit-icon.css);
@import url(./__description/profile__description.css);
@import url(./__add/profile__add.css);
@import url(./__add-icon/profile__add-icon.css);
.profile {
  max-width: 68.75%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
}

@media screen and (max-width: 1024px) {
  .profile {
    max-width: 85%;
    justify-content: space-between;
  }
}
@media screen and (max-width: 958px) {
  .profile {
    max-width: 100%;
    flex-direction: column;
    padding: 42px 0 0 0;
  }
}
